.header-container {
    position: absolute;
    top: 0px;
    width: 100%;
    display: flex;
}

/*.header-container h3 {*/
/*    padding: 0px;*/
/*    margin: 0px;*/
/*}*/

.header-logo {
    position: fixed;
    left: 0px;
    padding-top: 3rem;
    padding-left: 5rem;
}

.header-content {
    display: flex;
    flex-direction: row;
    padding-top: 3rem;
}

.header-line {
    margin-top: -0.5rem;
    opacity: 0;
    transition: opacity 200ms linear;
}

.header-link:hover .header-line {
    opacity: 1;
}

.header-line-active {
    margin-top: -0.5rem;
    display: block;
}

.header-no-link {
    pointer-events: none;
    cursor: default;
}

#slash {
    color: var(--light-grey);
    padding-right: 0.5rem;
    cursor: default;
}

#root {
    text-align: center;
}